import React, { useEffect, useState } from 'react'
import img from "../assets/images/fake-dashboard.jpg"
import EngageMent from '../components/charts/EngageMent'
import Perfomance from '../components/charts/Perfomance'
import SalesConversion from '../components/charts/SalesConversion'
import { DateRangePicker } from 'react-date-range'
import Dropdown from 'react-bootstrap/Dropdown';
import { Helmet } from "react-helmet";
import "./dashboard.css"
import { useSelector } from 'react-redux'
const DashBoard = ({ state, setState }) => {
    const today = new Date();
    const yesterday = new Date(today);
    yesterday.setDate(today.getDate() - 1);
    const authData = useSelector((state => state.auth.user))
    const [xstate, setxState] = useState([
        {
            startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
            endDate: yesterday,
            key: 'selection'
        }
    ]);
    const [dropdownOpen, setDropdownOpen] = useState(false);
    const toggleDropdown = () => setDropdownOpen(!dropdownOpen);

    const handleApply = () => {
        // Your apply logic here
        setDropdownOpen(false); // Close the dropdown
        setState(xstate)
    };

    useEffect(() => {
        setxState(state)
    }, [])


    return (
        <>
            <Helmet>
                <title>SWIRL | Dashboard</title>
            </Helmet>
            <div className='card '>
                <div className="row p-2" >
                    <div className="col">
                        <div className='d-flex'>
                            <ul className="nav nav-tabs" id="responsive-tabs" role="tablist">
                                <li className="nav-item" role="presentation">
                                    <button className="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home" type="button" role="tab" aria-controls="home" aria-selected="true">Shoppable Video</button>
                                </li>
                                <li className="nav-item d-none" role="presentation">
                                    <button className="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile" type="button" role="tab" aria-controls="profile" aria-selected="false" disabled>Live Stream</button>
                                </li>

                            </ul>
                            <div className='ms-auto d-flex align-items-center' >
                                <div className="dropdown">

                                    <Dropdown show={dropdownOpen} onToggle={toggleDropdown}>
                                        <Dropdown.Toggle id="dropdown-basic" className='bg-transparent text-dark border-dark'>
                                            {state[0].endDate !== null ? `${state[0].startDate.toString().slice(4, 15)} to ${state[0].endDate.toString().slice(4, 15)}` : "Filter by date"}
                                        </Dropdown.Toggle>

                                        <Dropdown.Menu>
                                            <DateRangePicker
                                                editableDateInputs={false}
                                                onChange={item => setxState([item.selection])}
                                                moveRangeOnFirstSelection={true}
                                                ranges={xstate}
                                                months={2}
                                                direction='horizontal'
                                                maxDate={yesterday}
                                                minDate={authData?.brand_id == "236633565" ? new Date(2024, 2, 15) : new Date(2024, 3, 5)}
                                            // rangeColors={['#17171C']}

                                            />
                                            <div className='w-100'>
                                                <button id="dropdown-basic" className='btn btn-sm btn-primary float-end me-3' onClick={handleApply}>Done</button>
                                                <button id="dropdown-basic" className='btn btn-sm btn-outline-primary float-end me-3' onClick={() => {
                                                    setState([
                                                        {
                                                            startDate: new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000),
                                                            endDate: yesterday,
                                                            key: 'selection'
                                                        }
                                                    ])
                                                }}>Reset</button>

                                            </div>
                                        </Dropdown.Menu>
                                    </Dropdown>
                                </div>


                            </div>
                        </div>
                        <div className="tab-content p-3" id="responsive-tabs-content">
                            <div className="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">
                                <ul className="nav nav-tabs" id="responsive-tabs" role="tablist">

                                    <li className="nav-item" role="presentation">
                                        <button className="nav-link active" id="profile-tab" data-bs-toggle="tab" data-bs-target="#engagement" type="button" role="tab" aria-controls="profile" aria-selected="false">Engagment</button>
                                    </li>
                                    <li className="nav-item" role="presentation">
                                        <button className="nav-link " id="home-tab" data-bs-toggle="tab" data-bs-target="#Performance" type="button" role="tab" aria-controls="home" aria-selected="true">Performance</button>
                                    </li>
                                    <li className="nav-item d-none" role="presentation">
                                        <button className="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#sconversion" type="button" role="tab" aria-controls="profile" aria-selected="false" disabled>Sales Conversion</button>
                                    </li>

                                </ul>
                                <div className="tab-content p-3" id="responsive-tabs-content">
                                    <div className="tab-pane fade show active" id="engagement" role="tabpanel" aria-labelledby="home-tab">
                                        <EngageMent selectedDate={state[0]} />

                                    </div>
                                    <div className="tab-pane fade" id="Performance" role="tabpanel" aria-labelledby="profile-tab">
                                        <Perfomance selectedDate={state[0]} />
                                    </div>
                                    <div className="tab-pane fade" id="sconversion" role="tabpanel" aria-labelledby="profile-tab">
                                        <SalesConversion selectedDate={state[0]} />
                                    </div>

                                </div>
                            </div>
                            <div className="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">
                                <div style={{ height: "60vh", width: "70vw", position: "absolute" }}>
                                    <img src={img} alt='coming_soon' style={{ height: "60vh", width: "70vw" }} />
                                </div>
                                <div style={{ height: "60vh", width: "70vw", backgroundColor: "rgba(0,0,0,0.6)", position: "relative", display: "grid", placeItems: "center" }}>

                                    <div className='text-center text-white'>
                                        <h2>Coming soon...</h2>
                                        <p>We are working on it.</p>
                                    </div>
                                </div>

                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default DashBoard